<template>
   <div>
      <section class="bg-white text-white pt-8">

         <h2 class="uppercase text-3xl pt-6 text-blue font-bold
            md:text-6xl
            lg:text-4xl">- hotel -</h2>

            <span class="capitalize font-Clarissa text-blue text-5xl">{{country}}</span>

         <!-- <figure class="pt-16">
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         
 
         <div class="bg-blue-180 py-10">         
            <!-- BUSCADOR DE PRECIOS -->
            <div class="flex flex-row justify-center items-end gap-8 border border-white rounded-4xl py-6 m-auto
            md:text-xl md:w-9/12">
                <div class="flex flex-col items-start gap-1">
                  <span class="font-semibold text-base text-left px-4">Rango de fecha a reservar</span>
                  <date-range-picker                    
                     ref="picker"
                     :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                     v-model="formData.dateRange"
                     :time-picker="false"
                     :autoApply="true"
                     :ranges="false"
                     class="dateRangePicker drp-detail"
                  >
                     <template #input="picker">
                        <span class="text-white text-xl w-full text-center">
                              {{ picker.startDate | dateFormat }} - {{ picker.endDate | dateFormat }}
                        </span>
                     </template>                
                  </date-range-picker>     
               </div>

               <stars :star_init="formData.stars" ></stars>       

               <div class="relative flex flex-col">
                  <span class="font-semibold text-base text-left leading-7 px-4">Habitaciones</span>
                  <div class="flex flex-col items-center justify-around gap-x-2 bg-transparent border border-white rounded-4xl text-white px-4 py-1
               md:flex-row">  
                        <i class="fas fa-door-open"></i>
                        <span> {{noRooms}} Habitacion(es) </span>
                        <i class="fa fa-user"></i>
                        <span> {{numperson}} Persona(s) </span>
                        <div class="cursor-pointer text-orange text-xl
                        md:ml-auto" @click="visible_person = !visible_person">
                           <i class="fas fa-edit"></i>
                        </div>                        
                  </div>
                  <div id="rooms" class="absolute right-0 flex gap-x-3 bg-white border-lightGray text-darkGray rounded-3xl px-3 z-10"
                     :class="{'visible-numperson': visible_person}" >                            
                     <num-person 
                        :r="this.formData.rooms" 
                        v-on:close="visible_person=false" 
                        v-on:addRoom="updNumRooms" 
                        v-on:subRoom="updNumRooms">
                     </num-person>
                  </div>                                   
               </div>

               <button class="uppercase bg-orange text-white text-xl border border-white rounded-4xl px-4 py-1"
               @click="searchPrices">buscar</button>               
            </div>
            
            <!-- DETALLE DEL HOTEL -->
            <div class="text-3xl py-6" v-if="loading">
               <i class="fa fa-spinner fa-pulse"></i>
            </div>
            <div class="container flex flex-col gap-4 p-10 md:flex-row" v-show="!loading">
               <!-- GALERIA DE IMAGENES -->
               <div class="w-1/5">
                  <Splide ref="hotelslider" class="hotel-slide-gallery" :options="slide_options" v-if="hotelInfo">
                     <SplideSlide v-for="(item, index) in hotelInfo.images" :key="index">
                        <img :src="item.img" :alt="'Sample' + index" width="240px" height="auto">
                     </SplideSlide>                     
                  </Splide>
               </div>

               <!-- INFORMACION DEL HOTEL -->               
               <article class="flex flex-col gap-3 items-start border-2 border-dashed border-white p-4
                  w-10/12">
                  <div class="flex justify-between w-full">
                     <div class="flex flex-col items-start">
                        <div class="flex gap-3">
                           <h2 class="uppercase text-2xl font-DIN-Bold
                           md:text-3xl">{{ hotelInfo.alias }}</h2>
                           <!-- Stars -->
                           <stars class="self-center ml-10" :star_init="hotelInfo.star"></stars>
                        </div>
                        <!-- Price From -->
                        <small>
                           Desde 
                           <b class="text-orange text-xl font-DIN-Bold md:text-2xl ">${{ hotelInfo.price_from }}</b> 
                           {{ (hotelInfo.per_pax) ? '[por persona / la noche]' : '[por habiacin]' }}
                        </small>       
                     </div>
                     <figure class="w-20">
                        <img class="border" :src="hotelInfo.location" alt="">
                     </figure>
                  </div>
                  <!-- Description -->
                  <p class="text-left py-4">{{ hotelInfo.description }} Lorem ipsum dolor sit amet consectetur adipisicing elit. Deleniti, ipsam molestias! Earum a molestiae dicta nostrum, molestias nisi, eum quia consectetur explicabo quos expedita! Et facere odio laudantium pariatur suscipit!</p>

                  <!-- CheckIn - Checkout -->
                  <div class="flex gap-3">
                     <b class="">Entrada: </b> {{ hotelInfo.check_in }}
                     <b class="">Salida: </b> {{ hotelInfo.check_out }}
                  </div>

                  <b class="border-b">Otras Caractersticas</b>
                  <div class="flex gap-3">
                     <span class="flex gap-4 justify-between" v-if="(hotelInfo.buildings > 0)"><b>Edificio(s): </b> {{ hotelInfo.buildings }}</span>
                     <span class="flex gap-4 justify-between" v-if="(hotelInfo.houses > 0)"><b>Casa(s): </b> {{ hotelInfo.houses }}</span>
                     <span class="flex gap-4 justify-between" v-if="(hotelInfo.bungalows > 0)"><b>Bungalow(s): </b> {{ hotelInfo.bungalows }}</span>
                  </div>
                  <b v-if="(hotelInfo.children_allowed > 0)">Permite nios</b>
                  <b v-if="(hotelInfo.children_allowed == 0)">No permite nios</b>

                  <hr class="w-full border-dashed mt-3">  
                  <div class="flex md:flex-row flex-col gap-2 w-full">
                     <div class="flex flex-col gap-2 items-start w-3/5">
                        <span class="flex gap-4 justify-between"><b class="uppercase">Telfono(s) </b> {{ hotelInfo.phone }}</span>
                        <span class="flex gap-4 justify-between"><b class="uppercase">Email(s) </b> {{ hotelInfo.email }}</span>
                        <span class="uppercase "><b>Direccin</b></span>
                        <span>{{ hotelInfo.address }}</span>  
                     </div>
                     <!-- <div class="w-2/5">
                        Mapa
                        <l-map v-if="showMap"
                           :zoom="13"
                           :center="center"
                           :options="mapOptions"
                           style="height: 80%"
                           @update:center="centerUpdate"
                           @update:zoom="zoomUpdate"
                        >
                           <l-tile-layer
                           :url="'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'"
                           :attribution="attribution"
                           />
                           <l-marker :lat-lng="withPopup">
                           <l-popup>
                              <div @click="innerClick">
                                 I am a popup
                                 <p v-show="showParagraph">
                                 Lorem ipsum dolor sit amet, consectetur adipiscing elit. Quisque
                                 sed pretium nisl, ut sagittis sapien. Sed vel sollicitudin nisi.
                                 Donec finibus semper metus id malesuada.
                                 </p>
                              </div>
                           </l-popup>
                           </l-marker>                           
                        </l-map>
                     </div> -->
                  </div>
                  <hr class="w-full border-dashed mb-3"> 
                     
                  <!-- Precios -->
                  <h3 class="flex gap-3 items-center text-xl">
                     <b>Precios en el rango de fecha: </b>
                     (<small>{{ formData.dateRange.startDate | dateFormat }}</small>-<small>{{ formData.dateRange.endDate | dateFormat }}</small>)
                  </h3>
                  <div class="text-3xl py-6" v-if="loadingPrices">
                     <i class="fa fa-spinner fa-pulse"></i>
                  </div>
                  <div class="w-full flex flex-col gap-3" v-show="(combinations && !loadingPrices)">          
                     <hotel-table-price :borderType="'border border-solid'" :rooms="combinations"></hotel-table-price>
                     <button class="uppercase font-DIN-Bold border border-white text-white rounded-75 px-8 py-1
                     hover:shadow-h-hover-blue-md hover:bg-white hover:text-blue ml-auto "
                     @click="sendRequest"
                     v-show="(combinations)">
                     Enviar solicitud</button>                  
                  </div>      
               </article>               
            </div>
         </div>
      </section>
   </div>
</template>
<script>
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
import '@/assets/css/fonts.css'

import { latLng } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";
import 'leaflet/dist/leaflet.css';


import moment from 'moment'

import NumPerson from '@/components/searcher/NumPerson'
import Stars from '@/components/searcher/Stars'
import HotelTablePrice from '@/components/HotelTablePrice'
import Authorization from '@/services/user/auth.service'

export default {
   name: 'ProductsDetail',
   filters: {
        dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
        }
   },
   components: {
      DateRangePicker,
      NumPerson,
      HotelTablePrice,
      Stars,
      Splide,
      SplideSlide,
      // map
      LMap,
      LTileLayer,
      LMarker,
      LPopup
   },
   data() {
      return {
         slide_options: { 
            direction   : 'ttb',                                                   
            rewind      : true,
            wheel       : true,
            pagination  : false,
            autoplay    : true,
            arrows      : false,
            perPage     : 3,                  
            height       : '50rem',
            width       : '20rem',
            fixedWidth  : '15rem',
            fixedHeight  : '15rem'                       
         },         
         loading: false,
         loadingPrices: false,
         hotel_id: 0,
         visible_person: false,         
         hotelInfo: {},    
         noRooms: 1,     
         country: '', 
         prices: {},
         request_obj: {},
         formData: {
               rooms: [{
                  adults: 1,
                  children: 0,
                  infants: 0,
               }],
               dateRange: {
                  startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                  endDate: moment().add(4, 'days').format('DD MMM YYYY')
               },  
               destination: {
                  value: '',
                  text: ''
               },    
               hotel: {
                  value: '',
                  text: ''
               }, 
               stars: 3           
         },  
         //map
         // center: latLng(47.41322, -1.219482),
         // attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
         // withPopup: latLng(47.41322, -1.219482),
         // withTooltip: latLng(47.41422, -1.250482),
         // currentZoom: 11.5,
         // currentCenter: latLng(47.41322, -1.219482),
         // showParagraph: false,
         // mapOptions: {
         // zoomSnap: 0.5
         // },
         // showMap: true
      }
   },
   computed: {
      numperson() {
         let noPerson = 0
         for(let r of this.formData.rooms) {                
               noPerson += r.adults + r.children
         }
         return noPerson
      }, 
      combinations() {
         const comb_list = []
         // parseo las habitaciones por combinaciones de cada hotel
         this.formData.rooms.map((e,index) => {               
            if(comb_list.length-1 < index) {
               comb_list.push([])
            }          
            if(this.prices.hasOwnProperty('rooms')) {
               for(let r of this.prices.rooms) {
                  if(r.combination) {
                     r.combination.map(c => {                       
                        if(c) {
                           if(c[0].adults_quantity == e.adults &&
                              c[0].children_quantity == e.children &&
                              c[0].infants_quantity == e.infants) {
                                 // console.log(c.lenght)
                              comb_list[index].push({
                                 room_id: r.room_id,
                                 room: r.name,
                                 prices: r.total_prices[index],
                                 num_night: c.length-1,
                                 comb: e
                              })
                           }
                        }
                     })
                  }
               }
            }            
         })
         
         return (comb_list.length > 0) ? comb_list : false
      }
   },
   created() {    
      this.country =  this.$route.params.poleKey 
      this.getHotelinfo().then(() => {
         this.searchPrices()
      })
   },
   methods: {
      async getHotelinfo() {         
         const am = 'iron'
         const amenities = [
            require("@/assets/img/amenities/" + am + ".png")
         ]
         const hotel_keyword = this.$route.params.keyword      
         // Buscando informacion del hotel         
         this.loading = true
         this.hotelInfo.images = [
            {img: require("@/assets/img/block1.png")},
            {img: require("@/assets/img/block2.png")},
            {img: require("@/assets/img/block3.png")},
            {img: require("@/assets/img/block2.png")},
            {img: require("@/assets/img/block1.png")},
         ] 
         await Authorization.checkLogin(`/hotels/${hotel_keyword}`).then(e => {                        
            this.loading = false            
            if(e) {                                                                      
               this.hotelInfo = e.data                    
               this.hotelInfo.location = require(`@/assets/img/segment/${this.hotelInfo.hotel_location.keyword}.png`)
               this.hotelInfo.images = [
                  {img: require("@/assets/img/block1.png")},
                  {img: require("@/assets/img/block2.png")},
                  {img: require("@/assets/img/block3.png")},
                  {img: require("@/assets/img/block2.png")},
                  {img: require("@/assets/img/block1.png")},
               ]                              
            }             
         })
      },      
      updNumRooms(obj) {
         // console.log(obj)
         if(obj.action == 'add') {
               this.formData.rooms.push({
                  adults: 1,
                  children: 0,
                  infants: 0,
               })
         } else if (obj.action == 'del') {
               this.formData.rooms.splice(obj.pos, 1)
         }
         this.noRooms = this.formData.rooms.length                            
      },
      searchPrices() {
         // Searching by params         
         const search_params = {
            hotel_id: this.hotelInfo.id,	
            "date_from": moment(this.formData.dateRange.startDate).format('YYYY-MM-DD'),
            "date_to": moment(this.formData.dateRange.endDate).format('YYYY-MM-DD'),
            "pole_id": this.hotelInfo.pole_id,
            "rooms": this.formData.rooms
         }
         this.loadingPrices = true
         search_params.auth = 'bearer'
         Authorization.checkLogin('/search/hotels/prices', search_params, 'post').then(e => {
            this.loadingPrices = false
            this.product = 'hotel',
            this.country = 'cuba'
            if(e) { 
               this.prices = e.data[0]             
               this.request_obj = {
                  country: this.country,
                  pole: this.hotelInfo.pole.alias,
                  date: moment(this.formData.dateRange.startDate).format('YYYY-MM-DD') + '/' + moment(this.formData.dateRange.endDate).format('YYYY-MM-DD'),
                  product: 'Hoteles',
                  product_obj: {
                     name: this.hotelInfo.alias
                  },
                  id: this.prices[0].id,             
                  is_home_request: false  
               }
               console.log(e.data)                
            }
         })
      },
      sendRequest() {
         if(this.prices) {
            localStorage.removeItem('request_item')
            // verifico que se haya escogido alguna habitacion                       

            if(this.request_obj.hasOwnProperty('rooms')) {
               this.$router.push({name: 'SendRequest'})
            }
         }
      }
   }
}
</script>
<style scoped>
   .title {
      text-transform: uppercase;
      font-family: 'DINPro-Bold';
      font-size: 18px;
      margin-bottom: 5px;
   }

    #rooms {
        visibility: collapse;
        opacity: 0;
        top: 70px;
        padding-top: 0.5rem;    
        padding-bottom: 0.5rem;    
        border: 1px solid var(--light-gray);
    }

    .visible-numperson {                
        visibility: visible !important;
        opacity: 1 !important;
        transition: opacity 150ms;        
        transition-timing-function: cubic-bezier(.22,.48,.82,.61);
    }
    @media screen and (min-width: 768px) {
        .visible-form {
            height: 260px !important;
        }
    }
</style>